#about{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width:calc(100vw - 2rem)  ;
    margin:  0 1rem;
    padding: 1rem;
    background: white;

}

@media screen and (max-width:650px) {
    .plan .planImg{
        width: 95% !important;
       
        padding: 0;
        margin: 0.5rem 0;

}
}

@media screen and (max-width:500px) {
    #about{
       
        width:100%   ;
        margin:  0.5rem 0;
        padding: 5px;

    }
}
@media screen and (max-width:400px) {
    .brandTitle1 , .brandTitle2{
     font-size: 3rem;
    }

    .brandTitle , .objectif{
        width: 100%;
        margin: 0 !important;
        padding: 5px !important;
    }

    .brandTitle .aboutDesc1 ,  .brandTitle .aboutDesc2{
        width: 100%;
        text-align: left;
    }

    .objectif .objGlobTitle , .objectif .objGlobDesc {
        width: 100%;
        padding: 0;
        margin: 0;
        text-align: left;
    }

    .plan{
        padding: 5px !important;
       
    }

    .plan .planTitle{
        width: 100%;
        padding: 0;
        margin: 0.5rem 0;
        font-size: 1.3rem;
        text-align:left;
    }
    .plan .planImg{
            width: 95% !important;
           
            padding: 0;
            margin: 0.5rem 0;

    }
}

.aboutDesc1{
    margin: 0 auto;
    display: flex;
    text-align: center;
    justify-content: center;
    font-size: 1rem;
    width: 80%;
    color: #51ca94;
    margin-bottom: 0.5rem;
   
   
}
.aboutDesc2{
    margin: 0 auto;
    display: flex;
    text-align: center;
    justify-content: center;
    font-size: 1rem;
    width: 60%;
}

.separetor{
    width: 80%;
 
    margin:1rem auto;
    height:1px;
    background-color: rgb(193, 186, 186);
}

.objectif{
    margin: 0 auto;
    padding: 1rem;
    display: flex;
    width: 100%;
    max-width: 60rem;
    flex-direction: column;
}
.objTitle{
    
    color: #41C4AF;
    margin: 1rem 0;
}
.objGlobTitle{
    color:#58646C;
    font-weight: 500;
    margin: 0 2rem;
}
.objGlobDesc{
    padding: 0 2.2rem;
    width: 90%;
}

.objSpec{
    list-style: none;
    display: flex;
    flex-wrap: wrap;
}

.objSpecLI{
    width: 45%;
    padding: 1rem;
    display: flex;
    word-wrap: break-word;
}

@media screen and (max-width:780px) {
    .objSpecLI{
        width: 100%;
        padding:   0.5rem;
    }
    .aboutDesc{
        width: 100%;
        padding: 0.5rem;
    }
}


.objSpecLITitle{
    color:#58646C;
    font-weight: 500;
}

.objGlobDesc{
    text-align: left;
    width: 100%;
}


.plan{
    margin: 0 auto;
    padding: 1rem;
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    gap: 1rem;

    align-items: center;
    justify-content: center;
}


.plan .planTitle{
    width: 100%;
   
    text-align:center;
}

.planImg{
    width: 45%;
    border: 2px solid #41C4AF;
    object-fit: cover;
    border-radius: 2rem;
}

.plans{
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 1rem 0;
}

.plans li{
   
    margin: 0.5rem 1rem;
    display: flex;
    flex-direction: column;
}

.date{
    padding-left: 1rem;
    font-size: 0.8rem;
    color: rgb(176, 175, 173);
}

.location{
    font-size: 1.2rem;
}

.accordion{
    width: 100%;
    padding: 0.5rem;
    margin: 1rem 0;
    border: none;
    max-width: 70rem;
}